	module.exports = {"data":{"markdownRemark":{"html":"<p>The Linc project (based in NY) has an interesting article on their use of del.icio.us (a web service that maintains lists of links for you.) I really appreciated their technical description of everything they're doing with it, but it isn't a beach read. </p>\n<p><a title=\"LINC Support Grab Bag Archive: Are you del.icio.us?\" href=\"http://www.lincproject.org/toolkit/support/notebook/?p=6\">LINC Support Grab Bag Archive: Are you del.icio.us?</a></p>\n<p>Here's a bit:</p>\n<blockquote class=\"large\">\nAre you maintaining lists of links someplace and looking for a way to keep them up-to-date?\n<p>You might consider taking a look at del.icio.us, a free service that will allow you to aggregate links on their system, tag them with category names of your own choosing and then display them in other forums.</blockquote></p>","frontmatter":{"created_at":"January 13, 2005","path":"/2005/01/13/using-delicious","title":"Using Del.icio.us","tags":["delicious","tagging"]}}},"pathContext":{}}

